img {
  border-radius: 50%; }

button,
button:hover,
button:active,
button:focus {
  outline: none; }

.error-msg {
  font-size: 18px;
  margin-top: 20px; }

.container-center {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center; }

.form-control {
  background: #f5f5f5;
  border-color: #ccc !important;
  box-shadow: none !important;
  margin-right: 10px;
  border-radius: 0.25rem !important;
  box-shadow: 0px 0px 5px 2px #ccc !important; }
  .form-control:focus {
    background: #fff;
    box-shadow: none !important; }

.input-group {
  width: calc(100% - 20vw) !important;
  margin: 0 auto; }

button:focus,
.btn-primary,
.btn-primary:focus {
  box-shadow: none; }

@media screen and (max-width: 720px) {
  h1 {
    font-size: 30px !important; }
  .input-group {
    width: 100% !important; } }

.previewImg:hover {
  cursor: pointer; }

.artist-name {
  font-size: 22px; }

@media screen and (max-width: 720px) {
  img {
    width: 160px; } }

.modal-content {
  border-radius: 0;
  min-height: 100%;
  min-height: 100vh; }

audio:focus {
  outline: 0; }

@media (min-width: 576px) {
  .modal-dialog {
    min-width: 100%;
    min-height: 100%;
    margin: 0; } }

.related-artist-box {
  display: inline-block;
  margin: 15px;
  text-align: center; }

@media screen and (max-width: 720px) {
  .relatedImg {
    width: 120px; } }

footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  text-align: center; }

footer > div.container {
  position: relative;
  left: -50%; }

a,
a:hover,
a:focus {
  text-decoration: none; }

div.container > p {
  margin-bottom: 10px !important; }

@media screen and (max-width: 720px) {
  div.container > p {
    font-size: 12px; } }

body {
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

